import { Link } from "react-router-dom";
import { PrefetchLink } from "@/components/PrefetchLink";
import { Button } from "@/components/ui/button";
import { List, Calendar, User, Menu, LogOut, Shield, MapPin, Map, Store, ClipboardList, Search } from "lucide-react";
import logoFallback from "@/assets/xiaoxiong-logo-christmas.png";
import { useState, useEffect, useMemo, useCallback, memo } from "react";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { useAuth } from "@/contexts/AuthContext";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { LanguageSwitcher } from "@/components/LanguageSwitcher";
import { useTranslation } from "react-i18next";
import { useUserRole } from "@/hooks/useUserRole";
import { useProfile } from "@/hooks/useProfile";
import { useSiteSettings } from "@/hooks/useSiteSettings";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuLabel, DropdownMenuSeparator, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import NotificationsDropdown from "@/components/NotificationsDropdown";
const Navigation = () => {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const {
    user,
    signOut
  } = useAuth();
  const {
    t,
    i18n
  } = useTranslation();
  const {
    isAdmin,
    isSupervisor,
    isMerchant
  } = useUserRole();
  const {
    data: profile
  } = useProfile();
  const {
    settings
  } = useSiteSettings();
  const logoUrl = settings?.logo_url || logoFallback;
  const logoWidth = settings?.logo_width || 35;
  const logoHeight = settings?.logo_height || 35;

  // Load user's language preference from database on mount/user change
  useEffect(() => {
    if (profile?.language_preference && profile.language_preference !== i18n.language) {
      i18n.changeLanguage(profile.language_preference);
    }
  }, [profile, i18n]);
  const navItems = useMemo(() => {
    const items = [{
      to: "/",
      icon: MapPin,
      label: t("nav.map")
    }, {
      to: "/restaurants",
      icon: List,
      label: t("nav.restaurants")
    }, {
      to: "/activities",
      icon: Calendar,
      label: t("nav.activities")
    }, {
      to: "/map",
      icon: Map,
      label: t("nav.mapView")
    }];

    // Only show "Join Us" link when user is not logged in
    if (!user) {
      items.push({
        to: "/join-us",
        icon: Store,
        label: t("nav.joinUs")
      });
    }
    return items;
  }, [t, user]);
  const handleSignOut = useCallback(() => {
    signOut();
    setMobileMenuOpen(false);
  }, [signOut]);
  const closeMobileMenu = useCallback(() => {
    setMobileMenuOpen(false);
  }, []);
  return <nav className="fixed top-0 left-0 right-0 z-50 bg-transparent shadow-[0_2px_4px_rgba(0,0,0,0.15)]">
      <div className="w-full px-3 sm:px-4 lg:px-6 h-14 sm:h-16 flex items-center justify-between">
        <Link to="/" className="flex items-center gap-2">
          <img 
            src={logoUrl} 
            alt="Logo" 
            width={logoWidth}
            height={logoHeight}
            loading="eager"
            fetchPriority="high"
            decoding="async"
            style={{
              width: `${logoWidth}px`,
              height: `${logoHeight}px`
            }} 
            className="object-contain drop-shadow-md" 
          />
        </Link>

        {/* Desktop Navigation - Only show on large screens */}
        <div className="hidden lg:flex items-center gap-6">
          {navItems.map(item => <PrefetchLink key={item.to} to={item.to} className="flex items-center gap-2 text-primary hover:bg-black hover:text-white px-3 py-1.5 rounded-md transition-colors">
              <item.icon className="w-4 h-4" />
              <span>{item.label}</span>
            </PrefetchLink>)}
        </div>

        <div className="flex items-center gap-1 sm:gap-3">
          <LanguageSwitcher />
          
          {user && <NotificationsDropdown />}
          
          {user ? <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="ghost" size="icon" className="rounded-full h-8 w-8 sm:h-10 sm:w-10">
                  <Avatar className="w-7 h-7 sm:w-8 sm:h-8">
                    <AvatarImage src={profile?.avatar_url || user.user_metadata?.avatar_url} />
                    <AvatarFallback className="text-xs sm:text-sm">
                      {profile?.display_name?.[0]?.toUpperCase() || user.user_metadata?.display_name?.[0]?.toUpperCase() || user.email?.[0]?.toUpperCase()}
                    </AvatarFallback>
                  </Avatar>
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end">
                <DropdownMenuLabel>{t("nav.myAccount")}</DropdownMenuLabel>
                <DropdownMenuSeparator />
                <DropdownMenuItem asChild>
                  <PrefetchLink to="/profile" className="cursor-pointer">
                    <User className="w-4 h-4 mr-2" />
                    {t("nav.profile")}
                  </PrefetchLink>
                </DropdownMenuItem>
                {(isAdmin || isMerchant || isSupervisor) && <DropdownMenuItem asChild>
                    <PrefetchLink to="/dashboard" className="cursor-pointer">
                      <Shield className="w-4 h-4 mr-2" />
                      {t("nav.dashboard")}
                    </PrefetchLink>
                  </DropdownMenuItem>}
                <DropdownMenuSeparator />
                <DropdownMenuItem onClick={() => signOut()} className="cursor-pointer">
                  <LogOut className="w-4 h-4 mr-2" />
                  {t("nav.signOut")}
                </DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu> : <Button asChild className="hidden lg:flex bg-black text-white hover:bg-black/90 dark:bg-white dark:text-black dark:hover:bg-white/90">
              <PrefetchLink to="/auth">{t("nav.signIn")}</PrefetchLink>
            </Button>}

          {/* Mobile & Tablet Menu */}
          <Sheet open={mobileMenuOpen} onOpenChange={setMobileMenuOpen}>
            <SheetTrigger asChild className="lg:hidden">
              <Button size="icon" aria-label={t("nav.openMenu")} className="h-8 w-8 sm:h-10 sm:w-10 rounded-lg bg-foreground text-background hover:bg-foreground/80 dark:bg-background dark:text-foreground dark:border dark:border-border dark:hover:bg-muted">
                <Menu className="w-4 sm:w-5 h-4 sm:h-5" />
              </Button>
            </SheetTrigger>
            <SheetContent side="right" className="w-64 sm:w-80">
              <div className="flex flex-col gap-4 mt-8">
                {navItems.map(item => <PrefetchLink key={item.to} to={item.to} onClick={closeMobileMenu} className="flex items-center gap-3 text-lg text-muted-foreground hover:text-primary transition-colors">
                    <item.icon className="w-5 h-5" />
                    <span>{item.label}</span>
                  </PrefetchLink>)}
                
                <div className="border-t border-border my-4" />
                
                {user ? <>
                    <PrefetchLink to="/profile" onClick={closeMobileMenu} className="flex items-center gap-3 text-lg text-muted-foreground hover:text-primary transition-colors">
                      <User className="w-5 h-5" />
                      <span>{t("nav.profile")}</span>
                    </PrefetchLink>
                    {(isAdmin || isMerchant || isSupervisor) && <PrefetchLink to="/dashboard" onClick={closeMobileMenu} className="flex items-center gap-3 text-lg text-muted-foreground hover:text-primary transition-colors">
                        <Shield className="w-5 h-5" />
                        <span>{t("nav.dashboard")}</span>
                      </PrefetchLink>}
                    <button onClick={handleSignOut} className="flex items-center gap-3 text-lg text-muted-foreground hover:text-primary transition-colors">
                      <LogOut className="w-5 h-5" />
                      <span>{t("nav.signOut")}</span>
                    </button>
                  </> : <PrefetchLink to="/auth" onClick={closeMobileMenu} className="flex items-center gap-3 text-lg text-muted-foreground hover:text-primary transition-colors">
                    <User className="w-5 h-5" />
                    <span>{t("nav.signIn")}</span>
                  </PrefetchLink>}
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </nav>;
};
export default Navigation;